import { smallMineFieldParams } from 'constants/mineFieldsParams';
import useDidUpdate from 'hooks/useDidUpdate';
import produce, { Draft } from 'immer';
import { difference, range } from 'lodash-es';
import { useMemo, useState } from 'react';
import addId from 'utils/addId';
import NeighborsUtils from 'utils/neighborsUtils';

type DraftCallback = (draft: Draft<Cell[]>) => void;

const useMineField = () => {
  const [mineFieldParams, setMineFieldParams] = useState(smallMineFieldParams);

  const { width, height, minesCount } = mineFieldParams;
  const length = width * height;

  const emptyMineField: Cell[] = useMemo(
    () => Array.from({ length }, (item, index) => addId({ index })),
    [length]
  );

  const [mineField, setMineField] = useState(emptyMineField);

  const initialMineFieldStatus: MineFieldStatus = {
    hiddenMinesCount: minesCount,
  };

  const [mineFieldStatus, setMineFieldStatus] = useState(
    initialMineFieldStatus
  );

  const neighborsUtils = new NeighborsUtils(width, height);

  const getFloodFilledMineField = (cell: Cell, draftCallback?: DraftCallback) =>
    produce(mineField, (draft) => {
      draftCallback?.(draft);
      /* eslint-disable no-param-reassign */
      draft[cell.index].isVisible = true;

      (function floodFill(item: Cell) {
        if (neighborsUtils.canFloodFill(draft, item)) {
          neighborsUtils.getIndexes(item).forEach((index) => {
            const neighbor = draft[index];
            const { isMined, isVisible, isFlagged } = neighbor;

            if (!isMined && !isVisible && !isFlagged) {
              neighbor.isVisible = true;

              floodFill(neighbor);
            }
          });
        }
      })(cell);
    });

  const getBustedMineField = (draftCallback: DraftCallback) =>
    produce(mineField, (draft) => {
      draftCallback(draft);
      draft.forEach((item) => {
        const { isMined, isBusted, isFlagged } = item;

        if (!isMined && isFlagged) {
          item.isWrong = true;
        } else if (isMined && !isBusted && !isFlagged) {
          item.isVisible = true;
        }
      });
    });

  const setup = (cell: Cell) => {
    setMineField(
      getFloodFilledMineField(cell, (draft) => {
        const restIndexes = difference(range(length), [
          cell.index,
          ...neighborsUtils.getIndexes(cell),
        ]);
        const minedIndexes = new Set<number>();

        while (minedIndexes.size < minesCount) {
          minedIndexes.add(
            restIndexes[Math.floor(Math.random() * restIndexes.length)]
          );
        }

        minedIndexes.forEach((index) => {
          draft[index].isMined = true;
        });

        draft.forEach((item) => {
          if (!item.isMined) {
            item.value = neighborsUtils.countMined(draft, item);
          }
        });
      })
    );

    setMineFieldStatus((prevMineFieldStatus) => ({
      ...prevMineFieldStatus,
      isSetup: true,
    }));
  };

  const openCell = (cell: Cell) => {
    const { index, isMined } = cell;

    setMineField(
      isMined
        ? getBustedMineField((draft) => {
            draft[index].isBusted = true;
          })
        : getFloodFilledMineField(cell)
    );
  };

  const toggleFlag = ({ index, isFlagged }: Cell) => {
    setMineField((prevMineField) =>
      produce(prevMineField, (draft) => {
        draft[index].isFlagged = !isFlagged;
      })
    );

    setMineFieldStatus((prevMineFieldStatus) =>
      produce(prevMineFieldStatus, (draft) => {
        draft.hiddenMinesCount += isFlagged ? 1 : -1;
      })
    );
  };

  const openNeighbors = (cell: Cell) => {
    if (neighborsUtils.canFloodFill(mineField, cell)) {
      setMineField(getFloodFilledMineField(cell));
    } else if (neighborsUtils.canOpen(mineField, cell)) {
      setMineField(
        getBustedMineField((draft) => {
          neighborsUtils.getIndexes(cell).forEach((index) => {
            const neighbor = draft[index];
            const { isMined, isFlagged } = neighbor;

            if (isMined && !isFlagged) {
              neighbor.isBusted = true;
            } else if (!isMined && !isFlagged) {
              neighbor.isVisible = true;
            }
          });
        })
      );
    }
  };

  const reset = () => {
    setMineField(emptyMineField);
    setMineFieldStatus(initialMineFieldStatus);
  };

  useDidUpdate(() => {
    reset();
  }, emptyMineField);

  useDidUpdate(() => {
    if (mineField.some(({ isBusted }) => isBusted)) {
      setMineFieldStatus((prevMineFieldStatus) => ({
        ...prevMineFieldStatus,
        isBusted: true,
      }));
    } else if (
      !mineField.some(({ isMined, isVisible }) => !(isMined || isVisible))
    ) {
      setMineField((prevMineField) =>
        produce(prevMineField, (draft) => {
          draft.forEach((cell) => {
            if (cell.isMined) {
              cell.isFlagged = true;
            }
          });
        })
      );

      setMineFieldStatus((prevMineFieldStatus) => ({
        ...prevMineFieldStatus,
        isSolved: true,
        hiddenMinesCount: 0,
      }));
    }
  }, mineField);

  return {
    mineField,
    mineFieldParams,
    mineFieldStatus,
    setMineFieldParams,
    setup,
    openCell,
    toggleFlag,
    openNeighbors,
    reset,
  };
};

export default useMineField;
