import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classNames from 'classnames';
import { memo, SyntheticEvent } from 'react';

import styles from './cell.module.scss';

interface Props {
  cell: Cell;
  onClick?: (cell: Cell) => void;
  onMouseDown?: (event: SyntheticEvent<HTMLButtonElement>, cell: Cell) => void;
  onContextMenu?: (cell: Cell) => void;
}

const Cell = ({ cell, onClick, onMouseDown, onContextMenu }: Props) => {
  const { value, isVisible, isFlagged, isMined, isWrong, isBusted } = cell;

  const visibleStyles = classNames(styles.cell, styles.visible);

  const handleClick = () => {
    onClick?.(cell);
  };

  const handleContextMenu = () => {
    onContextMenu?.(cell);
  };

  const handleMouseDown = (event: SyntheticEvent<HTMLButtonElement>) => {
    onMouseDown?.(event, cell);
  };

  if ((isMined && isVisible) || isBusted || isWrong) {
    return (
      <button
        type="button"
        className={classNames(visibleStyles, {
          [styles.busted]: isBusted,
          [styles.wrong]: isWrong,
        })}
      >
        <FontAwesomeIcon icon={['fas', 'bomb']} />
      </button>
    );
  }

  if (isFlagged) {
    return (
      <button
        type="button"
        className={styles.cell}
        onContextMenu={handleContextMenu}
      >
        <FontAwesomeIcon icon={['far', 'flag']} />
      </button>
    );
  }

  if (!isVisible) {
    return (
      <button
        type="button"
        aria-label="cell"
        className={styles.cell}
        onClick={handleClick}
        onContextMenu={handleContextMenu}
      />
    );
  }

  if (!value) {
    return <button type="button" aria-label="cell" className={visibleStyles} />;
  }

  return (
    <button
      type="button"
      className={classNames(visibleStyles, styles[`value_${value}`])}
      onMouseDown={handleMouseDown}
    >
      {value}
    </button>
  );
};

export default memo(Cell);
