import Radio from 'components/Radio';
import mineFieldsParams from 'constants/mineFieldsParams';
import MinesweeperContext from 'contexts/minesweeper';
import useCallbackRef from 'hooks/useCallbackRef';
import { useContext } from 'react';

import styles from './mineFieldParams.module.scss';

const MineFieldParams = () => {
  const { mineFieldParams, setMineFieldParams } = useContext(
    MinesweeperContext
  );

  const handleMineFieldParamsChange = useCallbackRef(setMineFieldParams);

  return (
    <form className={styles.mine_field_params}>
      {mineFieldsParams.map((item) => {
        const { name, id } = item;

        return (
          <Radio
            key={id}
            item={item}
            name="mine-field-params"
            className={styles.radio}
            checked={name === mineFieldParams.name}
            onChange={handleMineFieldParamsChange}
          />
        );
      })}
    </form>
  );
};

export default MineFieldParams;
